.menu {
  background-color: lavender;
  list-style: none;
  display: flex;
}

.link {
  display: inline-block;
  text-decoration: none;
  color: blue;
  margin-left: 5px;
  padding: 10px 15px;
  border: 1px solid blue;
  border-radius: 10px;
}

.linkActive {
  composes: link;
  color: #fff;
  background-color: blue;
}
